* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

header {
  background: #43a8f0;
  color: #fff;
  padding: 7px;
}

.instructions{
  font-size: 1.2rem;
  display: flex;
  padding: 20px;
}

.button-container {
  display: flex;
  justify-content: center;
}

[type=button]:not(:disabled), [type=reset]:not(:disabled), [type=submit]:not(:disabled), button:not(:disabled) {
  cursor: pointer;
}

.btn {
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  text-align: center;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 0.375rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  color: var(--btn-color);
  background-color: var(--btn-bg);
}

.btn:hover {
  color: var(--btn-hover-color);
  background-color: var(--btn-hover-bg);
  border-color: var(--btn-hover-border-color);
}

.btn:focus {
  color: var(--btn-hover-color);
  background-color: var(--btn-hover-bg);
  border-color: var(--btn-hover-border-color);
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(var(--btn-focus-shadow-rgb), .5);
}

.btn:active {
  color: var(--btn-active-color);
  background-color: var(--btn-active-bg);
  border-color: var(--btn-active-border-color);
}

.primary {
  --btn-color: #fff;
  --btn-bg: #43a8f0;
  --btn-border-color: #43a8f0;
  --btn-hover-color: #fff;
  --btn-hover-bg: #116bf0;
  --btn-hover-border-color: #0a58ca;
  --btn-focus-shadow-rgb: 49,132,253;
  --btn-active-color: #fff;
  --btn-active-bg: #0a58ca;
  --btn-active-border-color: #0a53be;
  --btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --btn-disabled-color: #fff;
  --btn-disabled-bg: #0d6efd;
  --btn-disabled-border-color: #0d6efd;
}

.secondary {
  --btn-color: #37474b;
  --btn-bg: #b0b2b4;
  --btn-border-color: #3b3b3b;
  --btn-hover-color: #fff;
  --btn-hover-bg: #3f3e42;
  --btn-hover-border-color: #000000;
  --btn-focus-shadow-rgb: 131,83,256;
  --btn-active-color: #fff;
  --btn-active-bg: #b3b2b6;
  --btn-active-border-color: #000000;
  --btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --btn-disabled-color: #fff;
  --btn-disabled-bg: #acacac;
  --btn-disabled-border-color: #000000;
}

.input-group {
  display: flex;
  flex-direction: column;
}

input {
  border: 1px solid #ccc;
  outline: none;
  padding: 10px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
}

.card {
  box-shadow: 0 0 4px 0 rgb(0 0 0 / 8%), 0 2px 4px 0 rgb(0 0 0 / 12%);
  border-radius: 3px;
  background: #fff;
}

.image-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container img {
  display: inline-block;
  width: 100%;
}
